				<view class="title">{{ showInfo.type == 'input' ? 'æä»¤ä¸ä¼ è¡¨' : 'æä»¤è£å³è¡¨' }}</view>
				<uni-table border v-if="showInfo.type == 'input'">
					<uni-tr class="rowTitle">
						<uni-th align="center">åå</uni-th>
