						<uni-th align="center">ææ¥åæä»¤</uni-th>
					</uni-tr>
					<uni-tr class="row">
						<uni-td :rowspan="3" align="center">ç¬¬{{ showInfo.chessRound }}åå</uni-td>
						<uni-td :rowspan="2" align="center">{{ showInfo.roundPeriodName }}</uni-td>
						<uni-td :rowspan="2" align="center">
							<uv-upload
								:fileList="fileList"
								name="6"
