					</uni-tr>
				</uni-table>
				<uni-table border v-else class="recordTable">
					<view v-for="(value, key) in recordMap" :key="key + 'map'">
						<view v-for="(item, index) in value" :key="index">
							<uni-tr class="rowTitle" v-if="getMapIndexByKey(key) == 0 && index == 0">
								<uni-th align="center" class="col1" style="color: #ffffff">åå</uni-th>
								<uni-th align="center" class="col2" style="color: #ffffff">é¶æ®µ</uni-th>
								<uni-th align="center" class="col3" style="color: #ffffff">ææ¥åæä»¤</uni-th>
								<uni-th align="center" class="col4" style="color: #ffffff">åä½</uni-th>
								<uni-th align="center" class="col5" style="color: #ffffff">é¨éå¨ä½</uni-th>
								<uni-th
									align="center"
									class="col6"
									style="color: #ffffff"
									v-if="showInfo.type == 'record'"
								>
									è£å³ç»æ
								</uni-th>
								<uni-th
									align="center"
									class="col6"
									style="color: #ffffff"
									v-if="showInfo.type == 'judge'"
								>
									è£å³
								</uni-th>
							</uni-tr>

							<uni-tr class="row" v-for="(eitem, eindex) in item.historyVoList" :key="eindex">
								<uni-td
									v-if="eindex == 0"
									:rowspan="item.historyVoList.length"
									align="center"
									class="col1"
									:align="'center'"
								>
									ç¬¬{{ key }}åå
								</uni-td>
								<uni-td
									v-if="eindex == 0"
									:rowspan="item.historyVoList.length"
									align="center"
									class="col2"
								>
									{{ item.roundPeriod | roundPeriodFormatter }}
								</uni-td>
								<uni-td
									v-if="eindex == 0"
