								>
									<uv-image @click="imageClick(item.coverUrl)" :src="item.coverUrl"></uv-image>
								</uni-td>
								<uni-td align="center" class="col4">{{ eitem.chessPiecesNumber }}</uni-td>
								<uni-td align="center" class="col5">{{ eitem.actionDesc }}</uni-td>
								<uni-td v-if="showInfo.type == 'record'" align="center" class="col6">
									{{ eitem.attackResult }}
								</uni-td>
								<uni-td v-if="showInfo.type == 'judge'" align="center" class="col6">
									<button
										@click="judgeOneByOne(eitem, key, index, eindex)"
										:disabled="eitem.status == 2"
									>
										{{ eitem.status != 2 ? 'è£å³' : 'å·²è£å³' }}
									</button>
								</uni-td>
							</uni-tr>
						</view>
					</view>
				</uni-table>
				<view class="foot">
					<view v-if="showInfo.type == 'input'" style="flex: 1" @click="submitInstruct">ä¿å­æä»¤</view>
					<view style="background-color: #ea9d00; flex: 1" @click="close">å³é­</view>
				</view>
			</view>
		</uni-popup>
		<judge-fire-attack-table
			ref="judgeFireAttackTable"
			:actionInfo="actionInfo"
			@confirmJudge="confirmJudge"
		></judge-fire-attack-table>
		<judge-action-table
			ref="judgeActionTable"
			:actionInfo="actionInfo"
			@confirmJudge="confirmJudge"
		></judge-action-table>
		<judge-investigate-table
			ref="judgeInvestigateTable"
			:actionInfo="actionInfo"
			@confirmJudge="confirmJudge"
		></judge-investigate-table>
		<judge-communication-table
			ref="judgeCommunicationTable"
			:actionInfo="actionInfo"
			@confirmJudge="confirmJudge"
		></judge-communication-table>
		<judge-reactance-table
			ref="judgeReactanceTable"
			:actionInfo="actionInfo"
			@confirmJudge="confirmJudge"
		></judge-reactance-table>
		<judge-command-control-table
			ref="judgeCommandControlTable"
			:actionInfo="actionInfo"
			@confirmJudge="confirmJudge"
		></judge-command-control-table>
	</view>
