<script>
import { baseURL, getUserTokenStorage } from '@/api/http.js';
import { saveRoundCover, getHistoryTreeByRound } from '@/api/verdictRecord.js';
export default {
	name: 'judge-table',
	props: {
		showInfo: {
			type: Object,
			default() {
				return {
					type: '',
					chessRound: 0,
					roundPeriod: 0,
					roundPeriodName: '',
					verdictRecordId: undefined,
					campId: undefined
				};
			}
		},
		stageOptions: {
			type: Array,
			default() {
				return [];
			}
		}
	},
	watch: {
		showInfo: {
			handler(newVal) {
				if (newVal.type == 'record' || newVal.type == 'judge') {
					this.getRoundTree(newVal);
				}
			},
			deep: true
		}
	},
	data() {
		return {
			fileList: [],
			uploadCustomStyle: {},
			recordMap: {},
			actionInfo: {},
			key: '',
			index: -1,
			eindex: -1
		};
	},
	filters: {
		roundPeriodFormatter(val) {
			let stageOptions = [
				{ title: 'ä½æä¿é' },
				{ title: 'åæè¡å¨' },
				{ title: 'åè£ä¿é' },
				{ title: 'è®¤ç¥åä½æè¡å¨' }
			];
			return stageOptions[val - 1].title;
		}
	},
	mounted() {},
	methods: {
		judgeOneByOne(eitem, key, index, eindex) {
			this.key = key;
			this.index = index;
			this.eindex = eindex;
			this.actionInfo = eitem;
			if (eitem.actionMode == 10) {
				this.$refs.judgeActionTable.open();
			} else if (eitem.actionMode == 20) {
				this.$refs.judgeFireAttackTable.open();
			} else if (eitem.actionMode == 40) {
				this.$refs.judgeInvestigateTable.open();
			} else if (eitem.actionMode == 50) {
				this.$refs.judgeCommunicationTable.open();
			} else if (eitem.actionMode == 60) {
				this.$refs.judgeReactanceTable.open();
			} else if (eitem.actionMode == 70) {
				this.$refs.judgeCommandControlTable.open();
			}
		},
		open() {
			this.$refs.popup.open();
			// this.sign();
		},
		close() {
			this.$refs.popup.close();
		},
		imageClick(url) {
			uni.previewImage({
				urls: [url]
			});
		},
		async getRoundTree(item) {
			let res = await getHistoryTreeByRound(item);
			let resArray = res.data.data;
			resArray = resArray.filter((item) => item.historyVoList.length > 0);
			this.recordMap = this.groupBy(resArray, 'chessRound');
			console.log('this.recordMap: ', this.recordMap);
		},
		getMapIndexByKey(key) {
			// ä¾æ¬¡è·åmapå¯¹è±¡å¼
			let index = 0;
			for (var k in this.recordMap) {
				if (k == key) {
					return index;
				}
				index++;
			}
		},
		groupBy(arr, key) {
			let obj = {};
			arr.forEach((item) => {
				if (!obj[item[key]]) {
					obj[item[key]] = [];
				}
				obj[item[key]].push(item);
			});
			return obj;
		},

		deletePic(e) {
			this.fileList.splice(e.index, 1);
			let urlList = [];
			this.fileList.map((item) => {
				urlList.push(item.url);
			});
		},
		async afterRead(event) {
			// å½è®¾ç½® multiple ä¸º true æ¶, file ä¸ºæ°ç»æ ¼å¼ï¼å¦åä¸ºå¯¹è±¡æ ¼å¼afterRead
			let lists = [].concat(event.file);
			let fileListLen = this.fileList.length;
			lists.map((item) => {
				this.fileList.push({
					...item,
					status: 'uploading',
					message: 'ä¸ä¼ ä¸­'
				});
			});

			for (let i = 0; i < lists.length; i++) {
				const result = await this.uploadFilePromise(lists[i].url);
				let item = this.fileList[fileListLen];
				this.fileList.splice(
					fileListLen,
					1,
					Object.assign(item, {
						status: 'success',
						message: '',
						url: result
					})
				);
				fileListLen++;
			}
			let urlList = [];
			this.fileList.map((item) => {
				urlList.push(item.url);
			});
			console.log('this.fileList: ', this.fileList);
		},
		uploadFilePromise(avatarUrl) {
			let that = this;
			return new Promise((resolve, reject) => {
				uni.uploadFile({
					url: baseURL + 'system/upload',
					filePath: avatarUrl,
					name: 'file',
					header: {
						Authorization: getUserTokenStorage()
					},
					success: (uploadFileRes) => {
						let jsonRes = JSON.parse(uploadFileRes.data);
						setTimeout(() => {
							resolve(jsonRes.data.url);
						}, 1000);
					}
				});
			});
		},
		async submitInstruct() {
			if (this.fileList.length == 0) {
				return uni.showToast({
					title: 'è¯·ä¸ä¼ æä»¤å¾ç',
					icon: 'none',
					duration: 2500
				});
			}
			this.showInfo.coverUrl = this.fileList[0].url;
			let res = await saveRoundCover(this.showInfo);
			if (res.data.code == 200) {
				uni.showToast({
					title: 'ä¿å­æå',
					icon: 'none',
					duration: 2500
				});
				this.fileList = [];
				this.$refs.popup.close();
			} else {
				uni.showToast({
					title: 'ä¿å­å¤±è´¥',
					icon: 'none',
					duration: 2500
				});
			}
		},
		confirmJudge(notJudgeNumber) {
			this.recordMap[this.key][this.index].historyVoList[this.eindex].status = 2;
			if (notJudgeNumber == 0) {
				//ææè®°å½è£å³å®æ¯
				this.$emit('judgeComplete');
			}
		}
	}
};
</script>

